.tooltip {
  position: relative;
  cursor: pointer; }
  .tooltip:after {
    opacity: 0;
    pointer-events: none;
    transition: all 0.2s ease-out 0.15s;
    font-size: 12px;
    line-height: 1;
    background: #212529;
    border-radius: 2px;
    color: #f1f3f5;
    content: attr(aria-label);
    padding: 8px 10px;
    position: absolute;
    white-space: nowrap;
    z-index: 10; }
  .tooltip:before {
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-top-color: #212529;
    opacity: 0;
    pointer-events: none;
    transition: all 0.2s ease-out 0.15s;
    content: "";
    position: absolute;
    z-index: 10; }
  .tooltip:hover:before, .tooltip:hover:after, .tooltip:focus:before, .tooltip:focus:after {
    opacity: 1;
    pointer-events: none; }

